/*-----------------------------------
     remove browser autofill 
------------------------------------*/
input:-webkit-autofill,
input:-webkit-autofill:hover,
input:-webkit-autofill:focus,
input:-webkit-autofill:active {
  box-shadow: none;
  -webkit-box-shadow: none;
  background-color: none;
  transition: background-color 5000s ease-in-out 0s; }

/*-----------------------------------
              place holder
------------------------------------*/
.ph-c::-webkit-input-placeholder {
  text-align: center; }

.ph-c:-moz-placeholder {
  /* Firefox 18- */
  text-align: center; }

.ph-c::-moz-placeholder {
  /* Firefox 19+ */
  text-align: center; }

.ph-c:-ms-input-placeholder {
  text-align: center; }

.ph-r::-webkit-input-placeholder {
  text-align: right;
  font-family: "iransans";
  direction: rtl; }

.ph-r input::-webkit-input-placeholder {
  text-align: right;
  font-family: "iransans";
  direction: rtl; }

.ph-r:-moz-placeholder {
  /* Firefox 18- */
  text-align: right;
  font-family: "iransans";
